<template>
  <el-select v-model="value" placeholder="select time">
    <el-option
      v-for="item in options"
      :key="item.value"
      :label="item.label"
      :value="item.value">
    </el-option>
  </el-select>
</template>

<script>
  export default {
    data() {
      return {
        options: [{
          value: '选项1',
          label: '11:00'
        }, {
          value: '选项2',
          label: '12:00'
        }, {
          value: '选项3',
          label: '16:00'
        }, {
          value: '选项4',
          label: '17:00'
        },{
          value: '选项5',
          label: '18:00'
        },{
          value: '选项6',
          label: '19:00'
        }],
        value: ''
      }
    }
  }
</script>



