<template>
    <div class="block">
      <el-date-picker
        v-model="value2"
        align="right"
        type="date"
        placeholder="select date"
        default-value="2018-03-01"
        :picker-options="pickerOptions1">
      </el-date-picker>
    </div>
</template>

<script>
const dates = ["2018-03-01", "2018-03-02", "2018-03-03", "2018-03-04", "2018-03-05", "2018-03-06", "2018-03-07", "2018-03-08", "2018-03-09", "2018-03-10", "2018-03-11", "2018-03-12", "2018-03-13", "2018-03-14", "2018-03-15"]
.map(v => new Date(`${v} 00:00:00`).getTime())

export default {
  name: 'Day',
  data() {
      return {
        pickerOptions1: {
          disabledDate(date) {
            return !dates.includes(date.getTime())
          },
        },
        value1: '',
        value2: '',
      };
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



